import React, { memo } from 'react'

const ScenarioSvgItem = memo(({ Svg, data, selectedScenario }) => {
  const x = `${data.x}px`
  const y = `${data.y}px`

  const scenario =
    data.scenario && data.scenario[selectedScenario]
      ? data.scenario[selectedScenario]
      : null

  return <Svg x={x} y={y} scenario={scenario} />
})

export default ScenarioSvgItem
